<template>
  <div class="container my-3">
    <form @submit.prevent="handleLogin">
      <div v-if="errorMessage" class="alert alert-danger">
        {{ errorMessage }}
      </div>
      <div class="mb-3">
        <label for="username" class="form-label">ÏÇ¨Ïö©ÏûêID</label>
        <input
          type="text"
          id="username"
          class="form-control"
          v-model="username"
          required
        />
      </div>
      <div class="mb-3">
        <label for="password" class="form-label">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
        <input
          type="password"
          id="password"
          class="form-control"
          v-model="password"
          required
        />
      </div>
      <button type="submit" class="btn btn-primary">Î°úÍ∑∏Ïù∏</button>
    </form>
  </div>
</template>

<script>
import axios from "@/api";
import { useUserStore } from "@/store/user";
import { useRouter } from "vue-router";
import { ref } from "vue";

export default {
  setup() {
    const username = ref("");
    const password = ref("");
    const errorMessage = ref("");
    const userStore = useUserStore();
    const router = useRouter();

    const handleLogin = async () => {
      if (!username.value || !password.value) {
        errorMessage.value = "Î™®Îì† ÌïÑÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";
        return;
      }
      try {
        await axios.post("/user/login", {
          username: username.value,
          password: password.value,
        });
        await userStore.fetchUser();
        router.push("/");
      } catch (error) {
        // Î°úÍ∑∏Ïù∏ Ïã§Ìå® Ïãú Í≥†Ï†ï Î©îÏãúÏßÄ Ï∂úÎ†•
        errorMessage.value = "ÏïÑÏù¥Îîî ÌòπÏùÄ ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.";
      }
    };

    return { username, password, errorMessage, handleLogin };
  },
};
</script>
