import { createRouter, createWebHistory } from "vue-router";
import { useUserStore } from "@/store/user";
import QuestionList from "@/components/QuestionList.vue";
import QuestionDetail from "@/components/QuestionDetail.vue";
import QuestionForm from "@/components/QuestionForm.vue";
import UserLogin from "@/components/UserLogin.vue";
import UserSignup from "@/components/UserSignup.vue";

const routes = [
	{ path: "/", component: QuestionList },
	{ path: "/questions/:id", component: QuestionDetail },
	{
		path: "/questions/new",
		component: QuestionForm,
		meta: { requiresAuth: true },
	},
	{ path: "/login", component: UserLogin, meta: { guestOnly: true } },
	{ path: "/signup", component: UserSignup, meta: { guestOnly: true } },
];

const router = createRouter({
	history: createWebHistory(),
	routes,
});

//Î°úÍ∑∏Ïù∏ ÌïÑÏöî ÌéòÏù¥ÏßÄ Ï†ëÍ∑º Ï†úÌïú Î∞è Í≤åÏä§Ìä∏ Ï†ÑÏö© ÌéòÏù¥ÏßÄ Ï≤òÎ¶¨
router.beforeEach(async (to, from, next) => {
  const userStore = useUserStore();
  try {
    await userStore.fetchUser(); // Ïø†ÌÇ§ Í∏∞Î∞ò ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î∂àÎü¨Ïò§Í∏∞

    // Î°úÍ∑∏Ïù∏ ÌïÑÏöî ÌéòÏù¥ÏßÄÏù∏Îç∞ Î°úÍ∑∏Ïù∏ Ïïà ÎêòÏñ¥ ÏûàÏúºÎ©¥ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨ÎîîÎ†âÏÖò
    if (to.meta.requiresAuth && !userStore.currentUser) {
      next("/login");
    }
    // Î°úÍ∑∏Ïù∏Îêú ÏÇ¨Ïö©ÏûêÍ∞Ä Î°úÍ∑∏Ïù∏/ÌöåÏõêÍ∞ÄÏûÖ ÌéòÏù¥ÏßÄ Ï†ëÍ∑º Ïãú ÌôàÏúºÎ°ú Î¶¨ÎîîÎ†âÏÖò
    else if (to.meta.guestOnly && userStore.currentUser) {
      next("/");
    } else {
      next(); // Ï†ïÏÉÅ Ïù¥Îèô
    }
  } catch (error) {
    console.error("Ïú†Ï†Ä ÌôïÏù∏ Ï§ë Ïò§Î•ò:", error);
    next("/login");
  }
});

export default router;
