<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <div class="container-fluid">
      <a class="navbar-brand" href="/">ÏßàÎ¨∏Í≥º ÎãµÎ≥Ä</a>

      <div class="d-flex align-items-center">
        <!-- Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÌëúÏãú -->
        <span v-if="currentUser" class="me-3">
          ÏïàÎÖïÌïòÏÑ∏Ïöî, {{ currentUser }} Îãò!
        </span>

        <!-- Î°úÍ∑∏Ïù∏ÌïòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ ÌöåÏõêÍ∞ÄÏûÖ Î≤ÑÌäº Ï∂îÍ∞Ä -->
        <router-link
          v-if="!isAuthenticated"
          to="/signup"
          class="btn btn-outline-success me-2"
        >
          ÌöåÏõêÍ∞ÄÏûÖ
        </router-link>

        <!-- Î°úÍ∑∏Ïù∏ / Î°úÍ∑∏ÏïÑÏõÉ Î≤ÑÌäº -->
        <button
          class="btn btn-outline-danger me-2"
          v-if="isAuthenticated"
          @click="logout"
        >
          Î°úÍ∑∏ÏïÑÏõÉ
        </button>
        <router-link v-else to="/login" class="btn btn-outline-primary">
          Î°úÍ∑∏Ïù∏
        </router-link>
      </div>
    </div>
  </nav>
</template>

<script>
import axios from "@/api";
import { useUserStore } from "@/store/user";
import { computed, onMounted } from "vue";
import { useRouter } from "vue-router";

export default {
  setup() {
    const userStore = useUserStore();
    const router = useRouter();

    // Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Í∞ÄÏ†∏Ïò§Í∏∞
    const currentUser = computed(() => userStore.currentUser);

    // Î°úÍ∑∏Ïù∏ Ïó¨Î∂Ä ÌôïÏù∏
    const isAuthenticated = computed(() => !!userStore.currentUser);

    // Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
    const fetchUser = async () => {
      try {
        await userStore.fetchUser();
      } catch (error) {
        console.error("‚ùå ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:", error);
      }
    };

    onMounted(() => {
      fetchUser(); // Ïª¥Ìè¨ÎÑåÌä∏ ÎßàÏö¥Ìä∏ Ïãú Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú Ï≤¥ÌÅ¨
    });

    // Î°úÍ∑∏ÏïÑÏõÉ
    const logout = async () => {
      try {
        await axios.post("/user/logout", {}, { withCredentials: true });
        userStore.currentUser = null;
        router.push("/login");
      } catch (error) {
        console.error("Î°úÍ∑∏ÏïÑÏõÉ Ïã§Ìå®:", error.response?.data || error.message);
      }
    };

    return {
      userStore,
      currentUser,
      isAuthenticated,
      logout,
    };
  },
};
</script>
