<template>
  <div class="answer-container">
    <!-- ÎåìÍ∏Ä Î™©Î°ù -->
    <div v-for="answer in answers" :key="answer.id" class="answer-item">
      <div class="card my-3">
        <div class="card-body">
          <!-- ÏàòÏ†ï Î™®Îìú -->
          <div v-if="editMode === answer.comment.id">
            <textarea
              v-model="editedContent"
              class="form-control"
              rows="3"
            ></textarea>
            <div class="mt-2">
              <button @click="saveEdit(answer)" class="btn btn-success btn-sm">
                Ï†ÄÏû•
              </button>
              <button @click="cancelEdit" class="btn btn-secondary btn-sm">
                Ï∑®ÏÜå
              </button>
            </div>
          </div>

          <!-- ÏùºÎ∞ò Î™®Îìú -->
          <div v-else>
            <!-- ÎåìÍ∏Ä ÎÇ¥Ïö© -->
            <div class="card-text my-2" style="white-space: pre-line">
              {{ answer.comment?.content || "ÎÇ¥Ïö© ÏóÜÏùå" }}
            </div>

            <!-- ÏûëÏÑ±Ïûê Ï†ïÎ≥¥ & ÏûëÏÑ± ÎÇ†Ïßú -->
            <div class="d-flex justify-content-end mt-3">
              <div class="badge bg-light text-dark p-2 text-end">
                <div class="fw-bold">
                  {{ answer.comment?.author?.username || "ÏùµÎ™Ö" }}
                </div>
                <div>
                  created at: {{ formatDate(answer.comment?.createDate) }}
                </div>
                <div
                  v-if="
                    answer.comment?.updateDate &&
                    answer.comment?.updateDate !== answer.comment?.createDate
                  "
                >
                  updated at: {{ formatDate(answer.comment?.updateDate) }}
                </div>
              </div>
            </div>

            <!-- ÏàòÏ†ï/ÏÇ≠Ï†ú Î≤ÑÌäº -->
            <div class="d-flex justify-content-end mt-2">
              <button
                v-if="isAuthor(answer.comment)"
                @click="startEdit(answer)"
                class="btn btn-sm btn-warning me-1"
              >
                ÏàòÏ†ï
              </button>
              <button
                v-if="isAuthor(answer.comment)"
                @click="deleteAnswer(answer.comment.id)"
                class="btn btn-sm btn-danger"
              >
                ÏÇ≠Ï†ú
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- ÎåìÍ∏Ä ÏûÖÎ†• Ìèº: Î°úÍ∑∏Ïù∏Ìïú Í≤ΩÏö∞ÏóêÎßå Î≥¥ÏûÑ -->
    <div v-if="currentUser" class="answer-form">
      <textarea
        v-model="newAnswer"
        class="form-control"
        rows="3"
        placeholder="ÎãµÎ≥ÄÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
      ></textarea>
      <button @click="submitAnswer" class="btn btn-primary mt-2">
        ÎãµÎ≥Ä Îì±Î°ù
      </button>
    </div>
    <!-- Î°úÍ∑∏Ïù∏ÌïòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ ÏïàÎÇ¥ Î©îÏãúÏßÄ -->
    <div v-else class="text-center text-muted mt-3">
      ÎãµÎ≥Ä ÏûëÏÑ±ÏùÄ Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.
    </div>
  </div>
</template>

<script>
import { useUserStore } from "@/store/user";
import { ref, computed, onMounted } from "vue";
import axios from "@/api";
import dayjs from "dayjs";

export default {
  props: ["questionId"],
  setup(props) {
    const userStore = useUserStore();
    const answers = ref([]);
    const newAnswer = ref("");
    const editMode = ref(null);
    const editedContent = ref("");

    // Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥
    const currentUser = computed(() => userStore.currentUser);

    // ÎåìÍ∏Ä Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞
    const fetchAnswers = async () => {
      try {
        const response = await axios.get(
          `/questions/${props.questionId}/answers`
        );
        answers.value = response.data;
        console.log("ÎåìÍ∏Ä Î™©Î°ù:", response.data);
      } catch (error) {
        console.error("ÎãµÎ≥Ä Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:", error);
      }
    };

    // ÌòÑÏû¨ Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©ÏûêÍ∞Ä ÎåìÍ∏Ä ÏûëÏÑ±ÏûêÏù∏ÏßÄ ÌôïÏù∏
    const isAuthor = (comment) => {
      return (
        currentUser.value && comment?.author?.username === currentUser.value
      );
    };

    // ÎåìÍ∏Ä ÏàòÏ†ï ÏãúÏûë
    const startEdit = (answer) => {
      console.log("ÏàòÏ†ï Î≤ÑÌäº ÌÅ¥Î¶≠Îê® - answer ID:", answer.comment.id);
      editMode.value = answer.comment.id;
      editedContent.value = answer.comment?.content || "";
    };

    // ÎåìÍ∏Ä ÏàòÏ†ï Ï†ÄÏû•
    const saveEdit = async (answer) => {
      try {
        await axios.put(
          `/answers/${answer.comment.id}`,
          { content: editedContent.value },
          { withCredentials: true }
        );

        await fetchAnswers();
        editMode.value = null;
        editedContent.value = "";
      } catch (error) {
        console.error("ÏàòÏ†ï Ïã§Ìå®:", error);
      }
    };

    // ÏàòÏ†ï Ï∑®ÏÜå
    const cancelEdit = () => {
      editMode.value = null;
      editedContent.value = "";
    };

    // ÎåìÍ∏Ä ÏÇ≠Ï†ú
    const deleteAnswer = async (answerId) => {
      if (!confirm("Ï†ïÎßê ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?")) return;
      try {
        await axios.delete(`/answers/${answerId}`, { withCredentials: true });
        await fetchAnswers();
      } catch (error) {
        console.error("ÏÇ≠Ï†ú Ïã§Ìå®:", error);
      }
    };

    // ÎåìÍ∏Ä Îì±Î°ù
    const submitAnswer = async () => {
      if (!newAnswer.value.trim()) {
        alert("ÎãµÎ≥ÄÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
        return;
      }
      try {
        await axios.post(
          `/answers/${props.questionId}`,
          { content: newAnswer.value },
          { withCredentials: true }
        );

        await fetchAnswers();
        newAnswer.value = "";
      } catch (error) {
        console.error("ÎãµÎ≥Ä Îì±Î°ù Ïã§Ìå®:", error);
      }
    };

    onMounted(async () => {
      await userStore.fetchUser();
      await fetchAnswers();
    });

    return {
      answers,
      newAnswer,
      editMode,
      editedContent,
      currentUser,
      submitAnswer,
      deleteAnswer,
      startEdit,
      saveEdit,
      cancelEdit,
      isAuthor,
      formatDate: (dateString) => dayjs(dateString).format("YYYY-MM-DD HH:mm"),
    };
  },
};
</script>

<style scoped>
.answer-container {
  margin-top: 20px;
}
.answer-form {
  margin-top: 15px;
}
</style>
