<template>
  <div class="container my-3">
    <h2 class="border-bottom py-2">ÏßàÎ¨∏ ÏûëÏÑ±</h2>
    <div class="card my-3">
      <div class="card-body">
        <input
          v-model="subject"
          class="form-control my-2"
          placeholder="ÏßàÎ¨∏Ïùò Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
        />
        <textarea
          v-model="content"
          class="form-control my-2"
          rows="5"
          placeholder="ÏßàÎ¨∏Ïùò ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
        ></textarea>
        <button @click="submitQuestion" class="btn btn-success btn-sm">
          Îì±Î°ù
        </button>
        <router-link to="/" class="btn btn-secondary btn-sm mx-2">
          Ï∑®ÏÜå
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "@/api";

export default {
  data() {
    return {
      subject: "",
