    };
  },
  methods: {
    async submitQuestion() {
      // Îπà Ïπ∏ Ï≤¥ÌÅ¨
      if (!this.subject.trim() || !this.content.trim()) {
        alert("Ï†úÎ™©Í≥º ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
        return;
      }
      // Í∏∏Ïù¥ Ï†úÌïú Ï≤¥ÌÅ¨
      if (this.subject.trim().length > 200) {
        alert("Ï†úÎ™©ÏùÄ 200Ïûê Ïù¥ÎÇ¥Î°ú ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
        return;
      }
      if (this.content.trim().length > 20000) {
        alert("ÎÇ¥Ïö©ÏùÄ 20000Ïûê Ïù¥ÎÇ¥Î°ú ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
        return;
      }

      try {
        await axios.post(
          "/questions/create",
          { subject: this.subject, content: this.content },
          { withCredentials: true }
        );
        this.$router.push("/");
      } catch (error) {
        console.error("ÏßàÎ¨∏ Îì±Î°ù Ïã§Ìå®:", error);
      }
    },
  },
};
</script>
