<template>
  <div class="container my-3">
    <div class="d-flex justify-content-between align-items-center">
      <h2 class="border-bottom py-2">ÏßàÎ¨∏ Î™©Î°ù</h2>
      <router-link
        v-if="isAuthenticated"
        to="/questions/new"
        class="btn btn-primary btn-sm"
      >
        ÏßàÎ¨∏ ÏûëÏÑ±ÌïòÍ∏∞
      </router-link>
    </div>

    <table class="table">
      <thead class="table-dark">
        <tr class="text-center">
          <th>Î≤àÌò∏</th>
          <th style="width: 50%">Ï†úÎ™©</th>
          <th>Í∏ÄÏì¥Ïù¥</th>
          <th>ÏûëÏÑ±ÏùºÏãú</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="question in questions"
          :key="question.id"
          class="text-center"
        >
          <td>{{ question.id }}</td>
          <td class="text-start">
            <router-link :to="`/questions/${question.id}`" class="custom-link">
              {{ question.subject }}
              <span v-if="question.commentCount > 0" class="comment-count">
                [{{ question.commentCount }}]
              </span>
            </router-link>
          </td>
          <td>{{ question.author?.username || "ÏùµÎ™Ö" }}</td>
          <td>{{ formatDate(question.createDate) }}</td>
        </tr>
      </tbody>
    </table>

    <!-- ÌéòÏù¥Ïßï Ïª®Ìä∏Î°§ -->
    <div class="d-flex justify-content-center align-items-center my-3">
      <button
        @click="goToPage(page - 1)"
        :disabled="page === 0"
        class="btn btn-sm btn-secondary mx-1"
      >
        Ïù¥Ï†Ñ
      </button>

      <!-- Ï¢åÏ∏° ÌòÑÏû¨ ÏúàÎèÑÏö∞Ïùò ÏµúÏÉÅÏúÑ Î≤àÌò∏Í∞Ä Ï†ÑÏ≤¥ ÏµúÍ≥† Î≤àÌò∏(totalPages)Î≥¥Îã§ ÏûëÏúºÎ©¥ ÌëúÏãú -->
      <span v-if="visiblePageNumbers[0] < totalPages" class="mx-1">...</span>

      <!-- ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≤ÑÌäºÎì§ (ÎÇ¥Î¶ºÏ∞®Ïàú) -->
      <button
        v-for="n in visiblePageNumbers"
        :key="n"
        @click="goToPage(totalPages - n)"
        class="btn btn-sm mx-1"
        :class="{
          'btn-primary': page === totalPages - n,
          'btn-light': page !== totalPages - n,
        }"
      >
        {{ n }}
      </button>

      <!-- Ïö∞Ï∏° ÌòÑÏû¨ ÏúàÎèÑÏö∞Ïùò ÏµúÌïòÏúÑ Î≤àÌò∏Í∞Ä 1Î≥¥Îã§ ÌÅ¨Î©¥ ÌëúÏãú -->
      <span
        v-if="visiblePageNumbers[visiblePageNumbers.length - 1] > 1"
        class="mx-1"
        >...</span
      >

      <button
        @click="goToPage(page + 1)"
        :disabled="page >= totalPages - 1"
        class="btn btn-sm btn-secondary mx-1"
      >
        Îã§Ïùå
      </button>
    </div>

    <!-- Í≤ÄÏÉâ ÏûÖÎ†•Ï∞Ω -->
    <div class="input-group mt-3">
      <input
        v-model="searchKeyword"
        @keyup.enter="fetchQuestions"
        type="text"
        class="form-control"
        placeholder="Í≤ÄÏÉâÏñ¥Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî(Ï†úÎ™© & ÎÇ¥Ïö©(ÏßàÎ¨∏/ÎãµÎ≥Ä))"
      />
      <button @click="fetchQuestions" class="btn btn-outline-secondary">
        Í≤ÄÏÉâ
      </button>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from "vue";
import axios from "@/api";
import dayjs from "dayjs";
import { useUserStore } from "@/store/user";

export default {
  setup() {
    const userStore = useUserStore();
    const questions = ref([]);
    const page = ref(0);
    const pageSize = ref(10);
    const totalElements = ref(0);
    const searchKeyword = ref("");

    const isAuthenticated = computed(() => !!userStore.currentUser);

    // Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Ïàò (Í≥ÑÏÇ∞Îêú Í∞í, Ïòà: Math.ceil(totalElements / pageSize))
    const totalPages = computed(() =>
      Math.ceil(totalElements.value / pageSize.value)
    );

    // visiblePageNumbers: ÏµúÎåÄ 10Í∞úÏùò ÎÇ¥Î¶ºÏ∞®Ïàú ÌéòÏù¥ÏßÄ Î≤àÌò∏ (1-based)
    const visiblePageNumbers = computed(() => {
      const total = totalPages.value;
      const windowSize = 10;
      if (total <= windowSize) {
        // Ï†ÑÏ≤¥Í∞Ä 10 Ïù¥ÌïòÏù¥Î©¥ Ï†ÑÏ≤¥Î•º ÎÇ¥Î¶ºÏ∞®ÏàúÏúºÎ°ú Î∞òÌôò
        return Array.from({ length: total }, (_, i) => total - i);
      } else {
        // ÌòÑÏû¨ ÌéòÏù¥ÏßÄÏùò ÌëúÏãú Î≤àÌò∏ (ÎÇ¥Î¶ºÏ∞®Ïàú)
        // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ index(0-indexed) ‚Üí displayed = total - page
        const displayedCurrent = total - page.value;
        let startDisplayed = displayedCurrent + Math.floor(windowSize / 2);
        if (startDisplayed > total) startDisplayed = total;
        let endDisplayed = startDisplayed - windowSize + 1;
        if (endDisplayed < 1) {
          endDisplayed = 1;
          startDisplayed = endDisplayed + windowSize - 1;
          if (startDisplayed > total) startDisplayed = total;
        }
        const pages = [];
        for (let n = startDisplayed; n >= endDisplayed; n--) {
          pages.push(n);
        }
        return pages;
      }
    });

    const fetchCommentCount = async (questionId) => {
      try {
        const response = await axios.get(`/questions/${questionId}/answers`);
        return response.data.length;
      } catch (error) {
        console.error(`ÎåìÍ∏Ä Ïàò Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå® (ÏßàÎ¨∏ ID: ${questionId}):`, error);
        return 0;
      }
    };

    const fetchQuestions = async () => {
      try {
        const response = await axios.get(
          `/questions?page=${page.value}&kw=${searchKeyword.value}`,
          { withCredentials: true }
        );
        const questionList = response.data.content;

        const questionsWithCommentCount = await Promise.all(
          questionList.map(async (q) => ({
            ...q,
            commentCount: await fetchCommentCount(q.id),
          }))
        );

        questions.value = questionsWithCommentCount;
        totalElements.value = response.data.totalElements || 0;
      } catch (error) {
        console.error("ÏßàÎ¨∏ Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:", error);
      }
    };

    const goToPage = (p) => {
      if (p < 0 || p >= totalPages.value) return;
      page.value = p;
      fetchQuestions();
    };

    const prevPage = () => {
      if (page.value > 0) {
        page.value--;
        fetchQuestions();
      }
    };

    const nextPage = () => {
      if (page.value < totalPages.value - 1) {
        page.value++;
        fetchQuestions();
      }
    };

    onMounted(async () => {
      await userStore.fetchUser();
      fetchQuestions();
    });

    return {
      questions,
      page,
      pageSize,
      totalElements,
      searchKeyword,
      isAuthenticated,
      totalPages,
      visiblePageNumbers,
      fetchQuestions,
      prevPage,
      nextPage,
      goToPage,
      formatDate: (dateString) =>
        dayjs(dateString).format("YYYY-MM-DD HH:mm:ss"),
    };
  },
};
</script>

<style scoped>
.custom-link {
  color: black;
  text-decoration: none;
  transition: color 0.2s;
}

.custom-link:hover {
  color: #007bff;
  text-decoration: none;
}

.comment-count {
  color: gray;
  margin-left: 5px;
}
</style>
